    .secondaryBackground {
        background-size: 0;
        height: 100vh;
        background-color: rgba(#14132d, 0.8);
        @media (min-width: $screenWidthMobileBig) {
            background: no-repeat;
            background-size: contain;
            background-position: bottom;
            background-color: rgba(#14132d, 0.8);
        }
    }
    
    .pageHeading {
        position: relative;
        top: 20%;
        transform: translateY(-20%);
        -webkit-transform: translateY(-20%);
        -moz-transform: translateY(-20%);
        -ms-transform: translateY(-20%);
        -o-transform: translateY(-20%);
        width: 100%;
        margin: auto;
        @media(min-width: $screenWidthMobileBig) {}
    }
    
    @keyframes rotate {
        100% {
            transform: rotate(1turn);
        }
    }
    
    .pageSloganContainer {
        margin: auto;
        border-radius: 5px;
        max-width: 450px;
        padding: 0 40px 40px 40px;
        >* {
            color: $colorWhite;
        }
        width: 90%;
        @media(min-width: $screenWidthMobileBig) {
            width: 100%;
        }
    }
    
    .pageExpanation {
        font-size: $fontsizeSheep;
    }
    
    //=====================================================
    .contactWidgetsContainer {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        @media(min-width: $screenWidthMobileBig) {
            flex-direction: row;
        }
    }
    
    .contactWidgetContainer {
        display: inline-block;
        padding: 1.5px 10px 1.5px 1.5px;
        border-radius: 20px;
        margin-top: 10px;
        @media(min-width: $screenWidthMobileBig) {
            margin-right: 20px;
        }
    }
    
    .contactWidget {
        display: flex;
        align-items: center;
    }
    
    .widgetText {
        color: $colorWhiteExtra;
        font-size: $fontsizeSheep;
    }
    
    .githubButton {
        background-color: rgb(33, 33, 49);
        border: 3px solid $colorWhite;
        >* {
            color: $colorDarker;
        }
    }
    
    .twitterButton {
        background-color: #55ACEE;
        border: 3px solid #55ACEE;
    }
    
    .linkedinButton {
        background-color: #007AB9;
        border: 3px solid #007AB9;
    }
    
    .contatWidgetIcon {
        margin-right: 10px;
    }
    
    //@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@
    .sectionCompetences {
        margin: auto;
        margin-top: 60px;
        width: 95%;
        @media (min-width: $screenWidthMobileBig) {
            width: 90%;
        }
    }
    
    .sectionCompetenceTitleContainer {
        text-align: center;
    }
    
    .sectionCompetenceTitle {
        display: inline-block;
        font-weight: bold;
        text-align: center;
        padding: 0 10px;
        border-bottom: 10px solid $colorBlue;
    }
    
    .competencesContainer {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        max-width: 940px;
        align-items: center;
        justify-content: space-between;
        margin: auto;
        >div {
            margin: 10px auto;
            @media (min-width: $screenWidthMobileBig) {
                margin: 20px auto;
            }
        }
    }
    
    //@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@
    .sectionPost {
        margin: auto;
        margin-top: 60px;
        width: 95%;
        @media (min-width: $screenWidthMobileBig) {
            width: 90%;
        }
    }
    
    .headIllustration {
        display: none;
        @media (min-width: $screenWidthMobileBig) {
            display: block;
            max-width: 280px;
            margin: auto;
            filter: drop-shadow(0 0 0.9rem $colorYellow);
            -webkit-filter: drop-shadow(0 0 0.9rem $colorYellow);
        }
    }
    
    .sectionPostSloganContainer {
        max-width: 450px;
        text-align: center;
    }
    
    .postsButtonContainer {
        width: 100%;
        text-align: center;
    }
    
    .sectionPostTitleContainer {
        text-align: center;
    }
    
    .sectionPostTitle {
        font-weight: bold;
        text-align: center;
        display: inline-block;
        border-bottom: 10px solid $colorGray;
        margin-top: 0;
    }
    
    .sectionPostData {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        max-width: 900px;
        justify-content: space-between;
        align-items: center;
        margin: auto;
        margin-top: 20px
    }
    
    .postsButton {
        background-color: $colorBlue;
        color: $colorWhite;
        padding: 2px 40px;
        border-radius: 3px;
        font-size: $fontsizeCow;
        box-shadow: rgba($colorBlue, 0.50)0px 5px 15px;
        border: 2px solid $colorBlue;
        transition: 200ms;
        &:hover {
            color: $colorBlue;
            background-color: $colorWhite;
            transition: 200ms;
        }
    }
    
    //@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@
    .projectSection {
        padding: 20px 0 20px 0;
        display: flex;
        justify-content: center;
        flex-direction: column;
        margin-top: 60px;
        margin-bottom: -110px;
        min-height: 400px;
        background: no-repeat;
        background-size: cover;
        background-position: bottom;
        text-align: center;
    }
    
    .projectSectionTitle {
        color: $colorWhite;
        display: inline-block;
        border-bottom: 4px solid $colorGreenBright;
    }
    
    .tagsContainer {
        max-width: 800px;
        margin-bottom: 20px;
    }
    
    .singleTag {
        display: inline-block;
        background-color: rgba($colorGray, 0.2);
        padding: 4px;
        color: $colorWhite;
        border-radius: 5px;
        margin: 5px 5px;
        font-size: $fontsizeSheep;
    }
    
    .projectSectionDataContainer {
        max-width: 900px;
        margin: auto;
        text-align: center;
        font-size: $fontsizeSheep;
    }
    
    .projectsButton {
        background-color: transparent;
        color: $colorWhite;
        padding: 2px 40px;
        border-radius: 4px;
        font-size: $fontsizeCow;
        box-shadow: rgba($colorGreenBright, 0.60)0px 0px 20px;
        border: 2px solid $colorGreenBright;
        transition: 200ms;
        &:hover {
            color: $colorDarkest;
            background-color: $colorGreenBright;
            transition: 200ms;
        }
    }
    
    //rainbow
    //@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@
    .rainbow {
        position: relative;
        z-index: 0;
        overflow: hidden;
        background: rgba(rgb(33, 33, 49), 1);
        @media (min-width: $screenWidthMobileBig) {
            &::before {
                content: '';
                position: absolute;
                z-index: -2;
                left: -50%;
                top: -50%;
                width: 200%;
                height: 200%;
                background-repeat: no-repeat;
                background-size: 50%, 50%;
                background-position: 0 0, 100% 0, 100% 100%, 0 100%;
                background-image: linear-gradient($colorGreen, $colorVioletDark), linear-gradient($colorYellow, $colorVioletDark);
                animation: rotate 4s linear infinite;
            }
            &::after {
                content: '';
                position: absolute;
                z-index: -1;
                left: 6px;
                top: 6px;
                width: calc(100% - 12px);
                height: calc(100% - 12px);
                border-radius: 5px;
                background: rgba(rgb(33, 33, 49), 1);
            }
        }
    }